"use client";

interface SidebarProps {
  currentTab: "dashboard" | "chat" | "uploads";
  onTabChange: (tab: "dashboard" | "chat" | "uploads") => void;
}

export default function Sidebar({ currentTab, onTabChange }: SidebarProps) {
  const navItems = [
    { id: "dashboard" as const, label: "Dashboard", description: "PowerBI Analytics" },
    { id: "chat" as const, label: "AI Chat", description: "Conversational AI" },
    { id: "uploads" as const, label: "Uploads", description: "Files & Media" },
  ];

  return (
    <div className="w-64 bg-white border-r border-gray-200 flex flex-col">
      <div className="p-6 border-b border-gray-200">
        <h1 className="text-xl font-bold text-gray-900">AI Analytics</h1>
        <p className="text-sm text-gray-600 mt-1">Data Intelligence Platform</p>
      </div>
      
      <nav className="flex-1 p-4">
        <ul className="space-y-2">
          {navItems.map((item) => (
            <li key={item.id}>
              <button
                onClick={() => onTabChange(item.id)}
                className={`w-full text-left p-3 rounded-lg transition-colors duration-200 ${
                  currentTab === item.id
                    ? "bg-gray-900 text-white"
                    : "text-gray-700 hover:bg-gray-100"
                }`}
              >
                <div className="font-medium">{item.label}</div>
                <div className={`text-sm mt-1 ${
                  currentTab === item.id ? "text-gray-300" : "text-gray-500"
                }`}>
                  {item.description}
                </div>
              </button>
            </li>
          ))}
        </ul>
      </nav>
      
      <div className="p-4 border-t border-gray-200">
        <div className="text-xs text-gray-500">
          Multi-modal AI Platform
        </div>
      </div>
    </div>
  );
}
