"use client";

import { useState } from "react";
import Sidebar from "@/components/Sidebar";
import PowerBIEmbed from "@/components/PowerBIEmbed";
import ChatInterface from "@/components/ChatInterface";
import VoiceRecorder from "@/components/VoiceRecorder";
import FileUploader from "@/components/FileUploader";
import PhotoUploader from "@/components/PhotoUploader";

type TabType = "dashboard" | "chat" | "uploads";

export default function Home() {
  const [currentTab, setCurrentTab] = useState<TabType>("dashboard");

  const renderContent = () => {
    switch (currentTab) {
      case "dashboard":
        return <PowerBIEmbed />;
      case "chat":
        return <ChatInterface />;
      case "uploads":
        return (
          <div className="space-y-8">
            <div className="bg-white rounded-lg shadow-sm border p-6">
              <h2 className="text-2xl font-semibold mb-4 text-gray-900">Voice Recording</h2>
              <VoiceRecorder />
            </div>
            <div className="bg-white rounded-lg shadow-sm border p-6">
              <h2 className="text-2xl font-semibold mb-4 text-gray-900">File Upload</h2>
              <FileUploader />
            </div>
            <div className="bg-white rounded-lg shadow-sm border p-6">
              <h2 className="text-2xl font-semibold mb-4 text-gray-900">Photo Upload</h2>
              <PhotoUploader />
            </div>
          </div>
        );
      default:
        return (
          <div className="flex items-center justify-center h-full">
            <div className="text-center">
              <h2 className="text-2xl font-semibold text-gray-900 mb-2">Page Not Found</h2>
              <p className="text-gray-600">The requested tab could not be found.</p>
            </div>
          </div>
        );
    }
  };

  return (
    <div className="flex h-screen bg-gray-50">
      <Sidebar currentTab={currentTab} onTabChange={setCurrentTab} />
      <main className="flex-1 overflow-auto p-6">
        {renderContent()}
      </main>
    </div>
  );
}
