"use client";

import { useState } from "react";

export default function PowerBIEmbed() {
  const [reportUrl, setReportUrl] = useState("");
  const [isLoading, setIsLoading] = useState(false);
  const [error, setError] = useState("");

  // Sample PowerBI public report URL for demonstration
  const sampleUrl = "https://app.powerbi.com/view?r=eyJrIjoiYjMzZTZkMzYtNWI4OS00YWY2LWE4YzMtOWQ2ZGY4ZGY4ZGY4IiwidCI6IjcyZjk4OGJmLTg2ZjEtNDFhZi05MWFiLTJkN2NkMDExZGI0NyIsImMiOjV9";

  const handleLoadReport = () => {
    if (!reportUrl.trim()) {
      setError("Please enter a PowerBI report URL");
      return;
    }
    
    setIsLoading(true);
    setError("");
    
    // Simulate loading delay
    setTimeout(() => {
      setIsLoading(false);
    }, 1000);
  };

  const loadSampleReport = () => {
    setReportUrl(sampleUrl);
    setIsLoading(true);
    setError("");
    
    setTimeout(() => {
      setIsLoading(false);
    }, 1000);
  };

  return (
    <div className="h-full flex flex-col bg-white rounded-lg shadow-sm border">
      <div className="p-6 border-b border-gray-200">
        <h1 className="text-2xl font-bold text-gray-900 mb-4">PowerBI Dashboard</h1>
        
        <div className="space-y-4">
          <div>
            <label htmlFor="reportUrl" className="block text-sm font-medium text-gray-700 mb-2">
              PowerBI Report URL
            </label>
            <div className="flex gap-2">
              <input
                id="reportUrl"
                type="url"
                value={reportUrl}
                onChange={(e) => setReportUrl(e.target.value)}
                placeholder="Enter PowerBI embed URL..."
                className="flex-1 px-3 py-2 border border-gray-300 rounded-md focus:outline-none focus:ring-2 focus:ring-gray-900 focus:border-transparent"
              />
              <button
                onClick={handleLoadReport}
                disabled={isLoading}
                className="px-4 py-2 bg-gray-900 text-white rounded-md hover:bg-gray-800 disabled:opacity-50 disabled:cursor-not-allowed transition-colors"
              >
                {isLoading ? "Loading..." : "Load Report"}
              </button>
            </div>
          </div>
          
          <button
            onClick={loadSampleReport}
            className="text-sm text-gray-600 hover:text-gray-900 underline"
          >
            Load Sample Report
          </button>
          
          {error && (
            <div className="p-3 bg-red-50 border border-red-200 rounded-md">
              <p className="text-sm text-red-600">{error}</p>
            </div>
          )}
        </div>
      </div>
      
      <div className="flex-1 p-6">
        {isLoading ? (
          <div className="h-full flex items-center justify-center">
            <div className="text-center">
              <div className="animate-spin rounded-full h-12 w-12 border-b-2 border-gray-900 mx-auto mb-4"></div>
              <p className="text-gray-600">Loading PowerBI report...</p>
            </div>
          </div>
        ) : reportUrl ? (
          <div className="h-full border border-gray-200 rounded-lg overflow-hidden">
            <iframe
              src={reportUrl}
              className="w-full h-full"
              frameBorder="0"
              allowFullScreen
              title="PowerBI Report"
              onError={() => setError("Failed to load PowerBI report. Please check the URL.")}
            />
          </div>
        ) : (
          <div className="h-full flex items-center justify-center">
            <div className="text-center max-w-md">
              <div className="w-16 h-16 bg-gray-100 rounded-full flex items-center justify-center mx-auto mb-4">
                <div className="w-8 h-8 bg-gray-300 rounded"></div>
              </div>
              <h3 className="text-lg font-medium text-gray-900 mb-2">No Report Loaded</h3>
              <p className="text-gray-600 mb-4">
                Enter a PowerBI report URL above to embed and view your analytics dashboard.
              </p>
              <p className="text-sm text-gray-500">
                Make sure the report has public access or proper embedding permissions.
              </p>
            </div>
          </div>
        )}
      </div>
    </div>
  );
}
