"use client";

import { useState, useRef } from "react";

interface UploadedPhoto {
  file: File;
  id: string;
  preview: string;
  status: "uploading" | "success" | "error";
  progress: number;
  error?: string;
}

export default function PhotoUploader() {
  const [uploadedPhotos, setUploadedPhotos] = useState<UploadedPhoto[]>([]);
  const [isDragOver, setIsDragOver] = useState(false);
  const fileInputRef = useRef<HTMLInputElement>(null);

  const allowedTypes = [
    'image/jpeg',
    'image/jpg',
    'image/png',
    'image/gif',
    'image/webp',
    'image/svg+xml',
  ];

  const maxFileSize = 5 * 1024 * 1024; // 5MB

  const validateFile = (file: File): string | null => {
    if (!allowedTypes.includes(file.type)) {
      return "File type not supported. Please upload JPEG, PNG, GIF, WebP, or SVG images.";
    }
    
    if (file.size > maxFileSize) {
      return "File size too large. Maximum size is 5MB.";
    }
    
    return null;
  };

  const createPreview = (file: File): Promise<string> => {
    return new Promise((resolve, reject) => {
      const reader = new FileReader();
      reader.onload = (e) => {
        if (e.target?.result) {
          resolve(e.target.result as string);
        } else {
          reject(new Error("Failed to read file"));
        }
      };
      reader.onerror = () => reject(new Error("Failed to read file"));
      reader.readAsDataURL(file);
    });
  };

  const simulateUpload = (photoId: string): Promise<void> => {
    return new Promise((resolve, reject) => {
      let progress = 0;
      const interval = setInterval(() => {
        progress += Math.random() * 25;
        
        setUploadedPhotos(prev => 
          prev.map(p => 
            p.id === photoId 
              ? { ...p, progress: Math.min(progress, 100) }
              : p
          )
        );
        
        if (progress >= 100) {
          clearInterval(interval);
          
          // Simulate random success/failure
          const success = Math.random() > 0.05; // 95% success rate
          
          setTimeout(() => {
            setUploadedPhotos(prev => 
              prev.map(p => 
                p.id === photoId 
                  ? { 
                      ...p, 
                      status: success ? "success" : "error",
                      progress: 100,
                      error: success ? undefined : "Upload failed. Please try again."
                    }
                  : p
              )
            );
            
            if (success) {
              resolve();
            } else {
              reject(new Error("Upload failed"));
            }
          }, 500);
        }
      }, 150);
    });
  };

  const handleFiles = async (files: FileList) => {
    const newPhotos: UploadedPhoto[] = [];
    
    for (let i = 0; i < files.length; i++) {
      const file = files[i];
      const validationError = validateFile(file);
      
      let preview = "";
      if (!validationError) {
        try {
          preview = await createPreview(file);
        } catch (error) {
          console.error("Failed to create preview:", error);
        }
      }
      
      const uploadedPhoto: UploadedPhoto = {
        file,
        id: `${Date.now()}-${i}`,
        preview,
        status: validationError ? "error" : "uploading",
        progress: 0,
        error: validationError || undefined,
      };
      
      newPhotos.push(uploadedPhoto);
    }
    
    setUploadedPhotos(prev => [...prev, ...newPhotos]);
    
    // Start uploads for valid files
    for (const uploadedPhoto of newPhotos) {
      if (uploadedPhoto.status === "uploading") {
        try {
          await simulateUpload(uploadedPhoto.id);
        } catch (error) {
          console.error("Upload failed:", error);
        }
      }
    }
  };

  const handleFileSelect = (e: React.ChangeEvent<HTMLInputElement>) => {
    const files = e.target.files;
    if (files) {
      handleFiles(files);
    }
    // Reset input value to allow selecting the same file again
    e.target.value = '';
  };

  const handleDrop = (e: React.DragEvent) => {
    e.preventDefault();
    setIsDragOver(false);
    
    const files = e.dataTransfer.files;
    if (files) {
      handleFiles(files);
    }
  };

  const handleDragOver = (e: React.DragEvent) => {
    e.preventDefault();
    setIsDragOver(true);
  };

  const handleDragLeave = (e: React.DragEvent) => {
    e.preventDefault();
    setIsDragOver(false);
  };

  const removePhoto = (photoId: string) => {
    setUploadedPhotos(prev => {
      const photoToRemove = prev.find(p => p.id === photoId);
      if (photoToRemove?.preview) {
        URL.revokeObjectURL(photoToRemove.preview);
      }
      return prev.filter(p => p.id !== photoId);
    });
  };

  const retryUpload = async (photoId: string) => {
    setUploadedPhotos(prev => 
      prev.map(p => 
        p.id === photoId 
          ? { ...p, status: "uploading", progress: 0, error: undefined }
          : p
      )
    );
    
    try {
      await simulateUpload(photoId);
    } catch (error) {
      console.error("Retry upload failed:", error);
    }
  };

  const formatFileSize = (bytes: number): string => {
    if (bytes === 0) return '0 Bytes';
    const k = 1024;
    const sizes = ['Bytes', 'KB', 'MB'];
    const i = Math.floor(Math.log(bytes) / Math.log(k));
    return parseFloat((bytes / Math.pow(k, i)).toFixed(1)) + ' ' + sizes[i];
  };

  return (
    <div className="space-y-6">
      <div
        className={`border-2 border-dashed rounded-lg p-8 text-center transition-colors ${
          isDragOver
            ? 'border-blue-400 bg-blue-50'
            : 'border-gray-300 hover:border-gray-400'
        }`}
        onDrop={handleDrop}
        onDragOver={handleDragOver}
        onDragLeave={handleDragLeave}
      >
        <div className="space-y-4">
          <div className="w-16 h-16 bg-gray-100 rounded-full flex items-center justify-center mx-auto">
            <div className="text-2xl">🖼️</div>
          </div>
          
          <div>
            <h3 className="text-lg font-medium text-gray-900 mb-2">
              Drop images here or click to browse
            </h3>
            <p className="text-gray-600 mb-4">
              Support for JPEG, PNG, GIF, WebP, and SVG images up to 5MB
            </p>
            
            <button
              onClick={() => fileInputRef.current?.click()}
              className="px-6 py-3 bg-gray-900 text-white rounded-lg hover:bg-gray-800 transition-colors font-medium"
            >
              Choose Images
            </button>
            
            <input
              ref={fileInputRef}
              type="file"
              multiple
              accept="image/*"
              onChange={handleFileSelect}
              className="hidden"
            />
          </div>
        </div>
      </div>

      {uploadedPhotos.length > 0 && (
        <div className="space-y-4">
          <h3 className="text-lg font-medium text-gray-900">Uploaded Images</h3>
          
          <div className="grid grid-cols-1 md:grid-cols-2 lg:grid-cols-3 gap-4">
            {uploadedPhotos.map((uploadedPhoto) => (
              <div
                key={uploadedPhoto.id}
                className="border border-gray-200 rounded-lg overflow-hidden bg-white"
              >
                {uploadedPhoto.preview && (
                  <div className="aspect-video bg-gray-100 relative">
                    <img
                      src={uploadedPhoto.preview}
                      alt={uploadedPhoto.file.name}
                      className="w-full h-full object-cover"
                    />
                    
                    <button
                      onClick={() => removePhoto(uploadedPhoto.id)}
                      className="absolute top-2 right-2 w-6 h-6 bg-black bg-opacity-50 text-white rounded-full flex items-center justify-center hover:bg-opacity-70 text-sm"
                    >
                      ✕
                    </button>
                  </div>
                )}
                
                <div className="p-4">
                  <div className="flex items-start justify-between mb-2">
                    <div className="flex-1 min-w-0">
                      <p className="text-sm font-medium text-gray-900 truncate">
                        {uploadedPhoto.file.name}
                      </p>
                      <p className="text-xs text-gray-500">
                        {formatFileSize(uploadedPhoto.file.size)}
                      </p>
                    </div>
                    
                    <div className="ml-2">
                      {uploadedPhoto.status === "success" && (
                        <div className="text-green-500 text-xs font-medium">✓</div>
                      )}
                      
                      {uploadedPhoto.status === "error" && (
                        <button
                          onClick={() => retryUpload(uploadedPhoto.id)}
                          className="text-blue-500 hover:text-blue-700 text-xs font-medium"
                        >
                          Retry
                        </button>
                      )}
                    </div>
                  </div>
                  
                  {uploadedPhoto.status === "uploading" && (
                    <div className="space-y-2">
                      <div className="flex justify-between text-xs text-gray-600">
                        <span>Uploading...</span>
                        <span>{Math.round(uploadedPhoto.progress)}%</span>
                      </div>
                      <div className="w-full bg-gray-200 rounded-full h-1.5">
                        <div
                          className="bg-blue-500 h-1.5 rounded-full transition-all duration-300"
                          style={{ width: `${uploadedPhoto.progress}%` }}
                        ></div>
                      </div>
                    </div>
                  )}
                  
                  {uploadedPhoto.error && (
                    <div className="text-xs text-red-600 mt-2">
                      {uploadedPhoto.error}
                    </div>
                  )}
                </div>
              </div>
            ))}
          </div>
        </div>
      )}
      
      <div className="text-sm text-gray-500">
        <p><strong>Supported formats:</strong> JPEG, PNG, GIF, WebP, SVG</p>
        <p><strong>Maximum file size:</strong> 5MB per image</p>
        <p><strong>Multiple images:</strong> You can upload multiple images at once</p>
        <p><strong>Preview:</strong> Images will be displayed with thumbnails after upload</p>
      </div>
    </div>
  );
}
