"use client";

import { useState, useRef } from "react";

interface UploadedFile {
  file: File;
  id: string;
  status: "uploading" | "success" | "error";
  progress: number;
  error?: string;
}

export default function FileUploader() {
  const [uploadedFiles, setUploadedFiles] = useState<UploadedFile[]>([]);
  const [isDragOver, setIsDragOver] = useState(false);
  const fileInputRef = useRef<HTMLInputElement>(null);

  const allowedTypes = [
    'text/csv',
    'application/vnd.ms-excel',
    'application/vnd.openxmlformats-officedocument.spreadsheetml.sheet',
    'application/pdf',
    'text/plain',
    'application/json',
  ];

  const maxFileSize = 10 * 1024 * 1024; // 10MB

  const validateFile = (file: File): string | null => {
    if (!allowedTypes.includes(file.type)) {
      return "File type not supported. Please upload CSV, Excel, PDF, TXT, or JSON files.";
    }
    
    if (file.size > maxFileSize) {
      return "File size too large. Maximum size is 10MB.";
    }
    
    return null;
  };

  const simulateUpload = (fileId: string): Promise<void> => {
    return new Promise((resolve, reject) => {
      let progress = 0;
      const interval = setInterval(() => {
        progress += Math.random() * 30;
        
        setUploadedFiles(prev => 
          prev.map(f => 
            f.id === fileId 
              ? { ...f, progress: Math.min(progress, 100) }
              : f
          )
        );
        
        if (progress >= 100) {
          clearInterval(interval);
          
          // Simulate random success/failure
          const success = Math.random() > 0.1; // 90% success rate
          
          setTimeout(() => {
            setUploadedFiles(prev => 
              prev.map(f => 
                f.id === fileId 
                  ? { 
                      ...f, 
                      status: success ? "success" : "error",
                      progress: 100,
                      error: success ? undefined : "Upload failed. Please try again."
                    }
                  : f
              )
            );
            
            if (success) {
              resolve();
            } else {
              reject(new Error("Upload failed"));
            }
          }, 500);
        }
      }, 200);
    });
  };

  const handleFiles = async (files: FileList) => {
    const newFiles: UploadedFile[] = [];
    
    for (let i = 0; i < files.length; i++) {
      const file = files[i];
      const validationError = validateFile(file);
      
      const uploadedFile: UploadedFile = {
        file,
        id: `${Date.now()}-${i}`,
        status: validationError ? "error" : "uploading",
        progress: 0,
        error: validationError || undefined,
      };
      
      newFiles.push(uploadedFile);
    }
    
    setUploadedFiles(prev => [...prev, ...newFiles]);
    
    // Start uploads for valid files
    for (const uploadedFile of newFiles) {
      if (uploadedFile.status === "uploading") {
        try {
          await simulateUpload(uploadedFile.id);
        } catch (error) {
          console.error("Upload failed:", error);
        }
      }
    }
  };

  const handleFileSelect = (e: React.ChangeEvent<HTMLInputElement>) => {
    const files = e.target.files;
    if (files) {
      handleFiles(files);
    }
    // Reset input value to allow selecting the same file again
    e.target.value = '';
  };

  const handleDrop = (e: React.DragEvent) => {
    e.preventDefault();
    setIsDragOver(false);
    
    const files = e.dataTransfer.files;
    if (files) {
      handleFiles(files);
    }
  };

  const handleDragOver = (e: React.DragEvent) => {
    e.preventDefault();
    setIsDragOver(true);
  };

  const handleDragLeave = (e: React.DragEvent) => {
    e.preventDefault();
    setIsDragOver(false);
  };

  const removeFile = (fileId: string) => {
    setUploadedFiles(prev => prev.filter(f => f.id !== fileId));
  };

  const retryUpload = async (fileId: string) => {
    setUploadedFiles(prev => 
      prev.map(f => 
        f.id === fileId 
          ? { ...f, status: "uploading", progress: 0, error: undefined }
          : f
      )
    );
    
    try {
      await simulateUpload(fileId);
    } catch (error) {
      console.error("Retry upload failed:", error);
    }
  };

  const formatFileSize = (bytes: number): string => {
    if (bytes === 0) return '0 Bytes';
    const k = 1024;
    const sizes = ['Bytes', 'KB', 'MB', 'GB'];
    const i = Math.floor(Math.log(bytes) / Math.log(k));
    return parseFloat((bytes / Math.pow(k, i)).toFixed(2)) + ' ' + sizes[i];
  };

  const getFileIcon = (fileType: string): string => {
    if (fileType.includes('csv')) return '📊';
    if (fileType.includes('excel') || fileType.includes('spreadsheet')) return '📈';
    if (fileType.includes('pdf')) return '📄';
    if (fileType.includes('json')) return '🔧';
    if (fileType.includes('text')) return '📝';
    return '📁';
  };

  return (
    <div className="space-y-6">
      <div
        className={`border-2 border-dashed rounded-lg p-8 text-center transition-colors ${
          isDragOver
            ? 'border-blue-400 bg-blue-50'
            : 'border-gray-300 hover:border-gray-400'
        }`}
        onDrop={handleDrop}
        onDragOver={handleDragOver}
        onDragLeave={handleDragLeave}
      >
        <div className="space-y-4">
          <div className="w-16 h-16 bg-gray-100 rounded-full flex items-center justify-center mx-auto">
            <div className="text-2xl">📁</div>
          </div>
          
          <div>
            <h3 className="text-lg font-medium text-gray-900 mb-2">
              Drop files here or click to browse
            </h3>
            <p className="text-gray-600 mb-4">
              Support for CSV, Excel, PDF, TXT, and JSON files up to 10MB
            </p>
            
            <button
              onClick={() => fileInputRef.current?.click()}
              className="px-6 py-3 bg-gray-900 text-white rounded-lg hover:bg-gray-800 transition-colors font-medium"
            >
              Choose Files
            </button>
            
            <input
              ref={fileInputRef}
              type="file"
              multiple
              accept=".csv,.xlsx,.xls,.pdf,.txt,.json"
              onChange={handleFileSelect}
              className="hidden"
            />
          </div>
        </div>
      </div>

      {uploadedFiles.length > 0 && (
        <div className="space-y-3">
          <h3 className="text-lg font-medium text-gray-900">Uploaded Files</h3>
          
          {uploadedFiles.map((uploadedFile) => (
            <div
              key={uploadedFile.id}
              className="border border-gray-200 rounded-lg p-4 bg-white"
            >
              <div className="flex items-center justify-between">
                <div className="flex items-center space-x-3 flex-1 min-w-0">
                  <div className="text-2xl">
                    {getFileIcon(uploadedFile.file.type)}
                  </div>
                  
                  <div className="flex-1 min-w-0">
                    <p className="text-sm font-medium text-gray-900 truncate">
                      {uploadedFile.file.name}
                    </p>
                    <p className="text-sm text-gray-500">
                      {formatFileSize(uploadedFile.file.size)}
                    </p>
                  </div>
                </div>
                
                <div className="flex items-center space-x-2">
                  {uploadedFile.status === "success" && (
                    <div className="text-green-500 text-sm font-medium">✓ Complete</div>
                  )}
                  
                  {uploadedFile.status === "error" && (
                    <button
                      onClick={() => retryUpload(uploadedFile.id)}
                      className="text-blue-500 hover:text-blue-700 text-sm font-medium"
                    >
                      Retry
                    </button>
                  )}
                  
                  <button
                    onClick={() => removeFile(uploadedFile.id)}
                    className="text-gray-400 hover:text-red-500 text-sm"
                  >
                    ✕
                  </button>
                </div>
              </div>
              
              {uploadedFile.status === "uploading" && (
                <div className="mt-3">
                  <div className="flex justify-between text-sm text-gray-600 mb-1">
                    <span>Uploading...</span>
                    <span>{Math.round(uploadedFile.progress)}%</span>
                  </div>
                  <div className="w-full bg-gray-200 rounded-full h-2">
                    <div
                      className="bg-blue-500 h-2 rounded-full transition-all duration-300"
                      style={{ width: `${uploadedFile.progress}%` }}
                    ></div>
                  </div>
                </div>
              )}
              
              {uploadedFile.error && (
                <div className="mt-2 text-sm text-red-600">
                  {uploadedFile.error}
                </div>
              )}
            </div>
          ))}
        </div>
      )}
      
      <div className="text-sm text-gray-500">
        <p><strong>Supported formats:</strong> CSV, Excel (.xlsx, .xls), PDF, TXT, JSON</p>
        <p><strong>Maximum file size:</strong> 10MB per file</p>
        <p><strong>Multiple files:</strong> You can upload multiple files at once</p>
      </div>
    </div>
  );
}
